  .check-task{
    padding-top:1vh !important;
    font-size: 14px;
    margin-bottom: 0px !important;
    vertical-align: middle !important;
  }
  
  /* Hide the browser's default checkbox */
  .check-task input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }
  
  /* Create a custom checkbox */
  .checkmark {
    position: absolute;
    height: 23px;
    width: 23px;
    background-color: #FFFFFF;
    border: 1px solid #34A38B;
    border-radius: 50%;
    
  }
  .checkmark-tabs{
    left: 6% !important;
  }
  .checkmark-place{
    top: 7px;
    left: 0;
    
  }
  
  
  .check-task:hover input ~ .checkmark {
    background-color: #FFFFFF;
  }
  
  /* When the checkbox is checked, add a blue background */
  .check-task input:checked ~ .checkmark {
    background: transparent linear-gradient(270deg, #44D7B6 0%, #329A80 100%) 0% 0% no-repeat padding-box;
  }
  
  /* Create the checkmark/indicator (hidden when not checked) */
  .checkmark:after {
    content: "";
    position: absolute;
    /* display: none; */
  }
  
  /* Show the checkmark when checked */
  .check-task input:checked ~ .checkmark:after {
    display: block;

  }
