.switch {
    position: relative;
    display: inline-block;
    width: 36px;
    height: 19px;
  }
  
  .switch input { 
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 14px;
    width: 14px;
    left: 4px;
    bottom: 2.3px;
    background-color: white;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #00AEB5;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #00AEB5;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(16px);
    transform: translateX(16px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }

